<template>
  <div class="hot_map_container" @mouseenter="hover()">
    <div id="main" style="width:100%;height: 400px"></div>
    <div class="text-container" v-if="ishover">
      <p>一大一大段话段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话一大段话</p>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default  {
    data () {
      return {
        ishover: false
      }
    },
    methods:{
      hover () {
        this.ishover = true
      },
      leave () {
        this.ishover = false
      }
    }
  }
</script>

<style>
.hot_map_container{
  height: 600px;
  position: relative;
}
.text-container{
  background-color: rgba(0,0,0,0.8);
  padding:20px;
  color: white;
  text-align: left;
  font-size: 14px;
  position: absolute;
  right: 0;
  top: 0;
  width: 30%;
  height: 600px;
  box-sizing: border-box;
}
</style>




